html {
  width: 100%;

}
body {
  /* 不要设置背景图在html，会被antd的默认背景覆盖 */
  background: url(/static/media/navbj.5a6f9b4e.png) no-repeat;
  background-size: cover;
  width: 100%;
  
}
/* box-sizing 是用于告诉浏览器如何计算一个元素是总宽度和总高度 */
/* *, *::before, *::after{ */
  /* antd会把所有的box-sizing设置为border-box(IE盒模型)。即宽度=内容+内边距+边框，高度也一样 */
  /* box-sizing: content-box !important; */
/* } */
* {
  padding: 0;
  margin: 0;
}
ul {
  list-style: none;
}
a {
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.clearfix:after{/*伪元素是行内元素 正常浏览器清除浮动方法*/
  content: "";
  display: block;
  height: 0;
  clear:both;
  visibility: hidden;
}
.clearfix{
  *zoom: 1;/*ie6清除浮动的方式 *号只有IE6-IE7执行，其他浏览器不执行*/
}
@font-face {
  font-family: "iconfont"; /* Project id 2561433 */
  src: url(/static/media/iconfont.8871f8c4.woff2) format('woff2'),
       url(/static/media/iconfont.275e5ac8.woff) format('woff'),
       url(/static/media/iconfont.407b3bae.ttf) format('truetype');
}

.iconfont {
  font-family: "iconfont" !important;
  font-size: 16px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

