/* header */
.header {
    width: 100%;
    height: 12vh;
    text-align: left;
    line-height: 12vh;
    border-radius: 1vw;
  }
  .header-antdDefault{
    /* visibility:hidden 隐藏，仍然占空间，可以通过DOM访问 */
  display: none;  /*隐藏，不占空间，可以通过DOM访问 */
  }
  .header-user{
    position: absolute;
    right: 0;
    top: -1vh;
    font-family: "Lucida Console", "Courier New", monospace;
  }
  .header-username{
    color:white;
    font-family: "Lucida Console", "Courier New", monospace;
    font-size: 2vw;
    opacity: 0.8;
  }
  @media screen and (min-width:1000px){
    .header-title {
      color: white;
      font-size: 2.5vw;
      opacity: 0.8;
      font-family: "Lucida Console", "Courier New", monospace;
      margin-left: 1vw;
    }
    .header-navBtn{
      display: none
    }
  }
  @media screen and (max-width:1000px){
    .header-space{
      width: 100%;
      height: 10vh;
    }
    .header {
      width: 100%;
      height: 7vh;
      line-height: 7vh;
      border-radius: 1vw;
      background-color: #090c10;
      position: fixed;
      top: 0;
      z-index: 1000;
      text-align: center;
    }
    .header-title {
      color: white;
      font-size: 3vh;
      opacity: 0.8;
      font-family: "Lucida Console", "Courier New", monospace;
    }
    .header-user{
      display: none;
    }
    .header-navBtn{
      position: fixed;
      left: 2vw;
      top: 0;
    }
    .header-navBtnImg{
      width: 8vw;
      height: 8vw;
    }
  }
/* nav-pc */
.nav-pc {
    border-radius: 1vw;
    width: 19%;
    height: 85vh;
    /* background: url('./common/img/nav-pcbj.png'); */
    float: left;
    border: 0.1vw solid #fffbf0;
  }
  .nav-pcActive{
    background-color: rgb(144, 170, 221);
  }
  
  .nav-pc li {
    margin-top: 6vh;
  }
  .nav-pc .nav-pc-icon {
    font-size: 2.4vw;
    position: relative;
    top: 0.2vw;
    padding-right: 0.6vw;
  }
  .nav-pc a {
    font-size: 1.5vw;
    color: rgba(255, 255, 255, 0.7);
    display: block;
   line-height: 6vh;
    padding-left: 1.5vw;
    height:6vh ;
  }
  @media screen and (min-width:1000px){
    .nav-pe {
      display: none;
    }
  }
  @media screen and (max-width:1000px){
    .nav-pc {
      display: none;
    }
    .nav-pe {
      width: 100%;
      overflow: hidden;
      position: fixed;
      top: 7vh;
      transition: height 1s;
      background-color: #090c10;
      z-index: 1000;
    }
    .nav-pe li {
      margin-top: 2vh;
      width: 0 auto;
      text-align: center;
    }
    .nav-pe ul {
      width: 100%;
    }
    .nav-pe a {
      font-size: 2.5  vh;
      color: white;
      display: block;
     line-height: 6vh;
      padding-left: 1.5vw;
      height:6vh ;
    }
  }
  @media screen and (max-width:1000px){
    .nav-user{
      position: absolute;
      right: 0;
      top: -1vh;
      font-family: "Lucida Console", "Courier New", monospace;
      margin-top: 4vh;
    }
    .nav-username{
      color:white;
      font-family: "Lucida Console", "Courier New", monospace;
      font-size: 4vw;
      opacity: 0.8;
    }
    .nav-username-btn{
      display: block
    }
  }
