/* header */
.header {
    width: 100%;
    height: 12vh;
    text-align: left;
    line-height: 12vh;
    border-radius: 1vw;
  }
  .header-antdDefault{
    /* visibility:hidden 隐藏，仍然占空间，可以通过DOM访问 */
  display: none;  /*隐藏，不占空间，可以通过DOM访问 */
  }
  .header-user{
    position: absolute;
    right: 0;
    top: -1vh;
    font-family: "Lucida Console", "Courier New", monospace;
  }
  .header-username{
    color:white;
    font-family: "Lucida Console", "Courier New", monospace;
    font-size: 2vw;
    opacity: 0.8;
  }
  @media screen and (min-width:1000px){
    .header-title {
      color: white;
      font-size: 2.5vw;
      opacity: 0.8;
      font-family: "Lucida Console", "Courier New", monospace;
      margin-left: 1vw;
    }
    .header-navBtn{
      display: none
    }
  }
  @media screen and (max-width:1000px){
    .header-space{
      width: 100%;
      height: 10vh;
    }
    .header {
      width: 100%;
      height: 7vh;
      line-height: 7vh;
      border-radius: 1vw;
      background-color: #090c10;
      position: fixed;
      top: 0;
      z-index: 1000;
      text-align: center;
    }
    .header-title {
      color: white;
      font-size: 3vh;
      opacity: 0.8;
      font-family: "Lucida Console", "Courier New", monospace;
    }
    .header-user{
      display: none;
    }
    .header-navBtn{
      position: fixed;
      left: 2vw;
      top: 0;
    }
    .header-navBtnImg{
      width: 8vw;
      height: 8vw;
    }
  }